<template>
  <v-content fill-height>
    <Home v-if="navigation.selected === 'Home'"></Home>
    <Solutions v-if="navigation.selected === 'Solutions'" :navigation="navigation"></Solutions>
    <AboutUs v-if="navigation.selected === 'AboutUs'"></AboutUs>
  </v-content>
</template>

<script>
import Home from '@/components/Containers/Home'
import AboutUs from '@/components/Containers/AboutUs'
import Solutions from '@/components/Containers/Solutions'

export default {
  props: {
    navigation: {
      type: Object,
      required: true
    }
  },
  data () {
    return {}
  },
  components: {
    Home: Home,
    AboutUs: AboutUs,
    Solutions: Solutions
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



