export default () => {
  const Jbot = {}

  Jbot.preview = null
  Jbot.recordingTimeMS = 5000

  Jbot.log = function (msg) {
    console.log(msg)
  }

  Jbot.setPreview = function (elm) {
    Jbot.preview = elm
  }

  Jbot.wait = function (delayInMS) {
    return new Promise(resolve => setTimeout(resolve, delayInMS))
  }

  Jbot.startRecording = function (stream, lengthInMS) {
    let recorder = new MediaRecorder(stream)
    let data = []
    recorder.ondataavailable = event => data.push(event.data)
    recorder.start()
    Jbot.log(recorder.state + ' for ' + (lengthInMS / 1000) + ' seconds...')
    let stopped = new Promise((resolve, reject) => {
      recorder.onstop = resolve
      recorder.onerror = event => reject(event.name)
    })
    let recorded = Jbot.wait(lengthInMS).then(
      () => recorder.state === 'recording' && recorder.stop()
    )
    return Promise.all([
      stopped,
      recorded
    ]).then(() => data)
  }

  Jbot.stop = function (stream) {
    stream.getTracks().forEach(track => track.stop())
  }

  Jbot.capture = async function () {
    return navigator.mediaDevices.getUserMedia({video: true, audio: true}).then(stream => {
      Jbot.preview.srcObject = stream
      Jbot.preview.captureStream = Jbot.preview.captureStream || Jbot.preview.mozCaptureStream
      return new Promise(resolve => { Jbot.preview.onplaying = resolve })
    }).then(() => Jbot.startRecording(Jbot.preview.captureStream(), Jbot.recordingTimeMS)).then(recordedChunks => {
      let recordedBlob = new Blob(recordedChunks, { type: 'video/webm' })
      Jbot.stop(Jbot.preview.captureStream())
      Jbot.log('Successfully recorded ' + recordedBlob.size + ' bytes of ' + recordedBlob.type + ' media.')
      return recordedBlob
    }).catch(Jbot.log)
  }
  return Jbot
}



