<template>
  <div id="app">
    <v-app>
      <Header :navigation="navigation"></Header>
      <Content :navigation="navigation"></Content>
      <Footer></Footer>
    </v-app>
  </div>
</template>

<script>
import Header from './components/Header'
import Content from './components/Content'
import Footer from './components/Footer'

export default {
  name: 'App',
  data () {
    return {
      navigation: {
        selected: 'Home',
        subnav: null
      }
    }
  },
  components: {
    Header: Header,
    Footer: Footer,
    Content: Content
  }
}
</script>

<style>
</style>



