<template>
  <v-container grid-list-xl>
    <v-layout align-center justify-center row fill-height>
      <DRCA v-if="navigation.selected === 'Solutions' & navigation.subnav === 'DRCA'"></DRCA>
      <ERA v-if="navigation.selected === 'Solutions' & navigation.subnav === 'ERA'"></ERA>
      <PyDriver v-if="navigation.selected === 'Solutions' & navigation.subnav === 'PyDriver'"></PyDriver>
      <SAS v-if="navigation.selected === 'Solutions' & navigation.subnav === 'SAS'"></SAS>
      <FaceAuthenticator v-if="navigation.selected === 'Solutions' & navigation.subnav === 'FaceAuthenticator'"></FaceAuthenticator>
    </v-layout>
  </v-container>
</template>

<script>
import DRCA from '@/components/Containers/SolutionContainer/DRCA'
import ERA from '@/components/Containers/SolutionContainer/ERA'
import PyDriver from '@/components/Containers/SolutionContainer/PyDriver'
import SAS from '@/components/Containers/SolutionContainer/SAS'
import FaceAuthenticator from '@/components/Containers/SolutionContainer/FaceAuthenticator'

export default {
  props: {
    navigation: {
      type: Object,
      required: true
    }
  },
  data () {
    return {}
  },
  components: {
    DRCA: DRCA,
    ERA: ERA,
    PyDriver: PyDriver,
    SAS: SAS,
    FaceAuthenticator: FaceAuthenticator
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



