<template>
  <v-flex xs12 sm10 md10>
    <v-card>
      <v-card-title primary-title>
        <div>
          <h3 class="headline mb-0">ERA</h3>
        </div>
      </v-card-title>

      <v-card-text>
        <v-layout align-center justify-center row wrap>
          <v-flex xs12 sm6 md6>
            <svg width="360" height="360"></svg>
          </v-flex>
          <v-flex xs12 sm6 md6>
            <div class="center">
              <video id="preview" width="100%" height="100%" autoplay muted></video>
            </div>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
  </v-flex>
</template>

<script>
import Jbot from '@/services/Jbot'
import * as d3 from 'd3'
import { execute } from '@/services/DataApi'

export default {
  data () {
    return {
      jbot: Jbot()
    }
  },
  mounted () {
    this.jEngineStart()
  },
  methods: {
    speak (msg) {
      window.speechSynthesis.speak(new SpeechSynthesisUtterance(msg))
    },
    executeCommand (command) {
      this.speak(command.response)
      console.log(command)
      this.jEngineExecute()
    },
    async jEngineExecute () {
      try {
        const response = await this.jbot.capture()
        const formData = new FormData()
        formData.append('files', response, 'chunk.webm')
        const extracted = await execute(formData)
        this.executeCommand(extracted.data)
      } catch (error) {
        this.speak(error.response.data.error)
        this.jEngineExecute()
      }
    },
    jEngineStart () {
      this.renderjBot()
      this.jbot.setPreview(document.getElementById('preview'))
      this.jEngineExecute()
    },
    renderjBot () {
      var svg = d3.select('svg')
      var width = +svg.attr('width')
      var height = +svg.attr('height')
      var angles = d3.range(0, 2 * Math.PI, Math.PI / 100)

      var path = svg.append('g')
        .attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
        .attr('fill', 'none')
        .attr('stroke-width', 10)
        .attr('stroke-linejoin', 'round')
        .selectAll('path').data(['cyan', 'magenta', 'yellow'])
        .enter()
        .append('path')
        .attr('stroke', function (d) {
          return d
        })
        .style('mix-blend-mode', 'darken')
        .datum(function (d, i) {
          return d3.radialLine()
            .curve(d3.curveLinearClosed)
            .angle(function (a) {
              return a
            })
            .radius(function (a) {
              var t = d3.now() / 500
              return 100 + Math.cos(a * 8 - i * 2 * Math.PI / 3 + t) * Math.pow((1 + Math.cos(a - t)) / 2, 3) * 32
            })
        })

      d3.timer(function () {
        path.attr('d', function (d) {
          return d(angles)
        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.center {
  margin: auto;
  border: 3px solid green;
  padding: 10px;
  height: 100%;
}
</style>



