<template>
  <v-toolbar class="white">
    <v-toolbar-title class="display-1">TwoGP Solutions</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-btn flat @click="navigate('Home', null)">Home</v-btn>
      <v-menu bottom offset-y>
        <v-btn flat slot="activator">Solutions</v-btn>
        <v-list>
          <v-list-tile
            v-for="item in items"
            :key="item"
            @click="navigate('Solutions', item)"
          >
            <v-list-tile-title v-text="item"></v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-btn flat @click="navigate('AboutUs', null)">About Us</v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
export default {
  props: {
    navigation: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      items: [
        'PyDriver', 'FaceAuthenticator', 'SAS', 'DRCA', 'ERA'
      ]
    }
  },
  methods: {
    navigate (navigator, subnav) {
      this.navigation.selected = navigator
      this.navigation.subnav = subnav
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.v-list__tile__title {
  cursor: pointer;
}
</style>



